.app-header-wrapper {
    background-color: $black2;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    & .logo {
        color: $red;
        & span:nth-child(1) {
            font-size: rem(35px);
            font-weight: 900;
        }
        & span:nth-child(2) {
            font-size: rem(25px);
            font-weight: 300;
            letter-spacing: 10px;
        }
    }
}
